"use strict";

var createHeader = function createHeader() {
  // New Html Tgas
  var menuContainerHtml = '<div class="menu_container"></div>';
  var IconLogin = '<i class="fas fa-sign-in-alt"></i>';
  var IconCart = '<i class="fas fa-shopping-cart"></i>';
  var MiniCartCont = '<div class="name-prod"></div>'; // Selectors for tags that already exists 

  var mainHeader = $('.menu.superior');
  var mainMenu = $('.menu.superior .nivel-um');
  var logo = $('#cabecalho .logo');
  var faleConosco = $('.barra-inicial .canais-contato .hidden-phone');
  var topLinks = $('#cabecalho .acoes-conta');
  var minhaConta = $('#cabecalho .acoes-conta > li + li');
  var headerSearchCont = $('#cabecalho .conteudo-topo .inferior');
  var BemVindo = $('#cabecalho .bem-vindo');
  var BemVindoSpan = $('#cabecalho .bem-vindo > span');
  var CarrinhoLinkHeader = $('#cabecalho .inferior .hidden-phone');
  var MiniCartProd = $('.carrinho-interno > ul > li'); //Actions

  mainHeader.prepend(menuContainerHtml);
  $('.menu_container').prepend(logo).append(mainMenu);
  topLinks.append(faleConosco);
  CarrinhoLinkHeader.before(BemVindo);
  BemVindo.prepend(IconLogin);
  BemVindoSpan.html('LOGAR NA LOJA');
  MiniCartProd.each(function () {
    $('.preco-produto', this).prepend($('.nome-produto', this));
  });
};

var setSelectedCategory = function setSelectedCategory() {
  var bodyClasses = $('body').attr('class').trim().split(' ');
  var getClass = bodyClasses.find(function (bc) {
    return bc.includes('categoria-');
  });

  if (getClass !== undefined) {
    var categoryId = $(".categoria-id-".concat(getClass.split('categoria-').pop()));
    categoryId.addClass('active');
  }
};

var TEMP__incrementSubMenu = function TEMP__incrementSubMenu() {
  for (var index = 0; index < 8; index++) {
    $('.nivel-dois').append("\n    <li class=\"categoria-marca-toni-armas\">\n      <a href=\"/marca/toni-armas.html\" title=\"TONI ARMAS\">\n        PRODUTO ".concat(index, " <span class=\"count\">(").concat(index, ")</span>\n      </a>\n    </li>\n    "));
  }
}; //Ready actions


jQuery(document).ready(function () {
  createHeader();
  setSelectedCategory(); //TEMP__incrementSubMenu()
}); //Load actions

jQuery(window).load(function () {}); //On resize actions

